@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

// :host {
//   flex-grow: 1;
// }

.custom-date-picker-wrapper {
  .form-control {
    cursor: pointer;
  }

  .date-select-wrapper {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    position: relative;
    display: flex;
    align-items: center;

    input {
      border: none;
      box-shadow: none;
      padding: 0;
      cursor: pointer;
      font-size: 13px;
      flex-grow: 1;
    }

    mat-icon {
      color: pick-color('primary', 'web');
      width: 20px;
      height: 20px;
      font-size: 20px;
      margin-bottom: 1px;
    }
  }

  .dropdown-wrapper {
    display: none;
    position: absolute;
    z-index: 50;
    width: 100%;
    margin: 6px 0 0 0;
    top: 90%;
    left: 0;

    &.show {
      display: block;
    }

    .dropdown {
      .date-range-wrapper {
        padding: 8px 20px;

        mat-date-range-input {
          visibility: hidden;
          opacity: 0;
          transform: scale(0);
          width: 0;
          height: 0;
        }

        .form-control {
          @include border-radius(8px);
          padding: 8px 12px;
          height: auto;
          line-height: 24px;
          position: relative;
          flex-shrink: 0;
          white-space: nowrap;
          min-width: 200px;
        }

        .date-icon {
          flex-shrink: 0;
          width: 20px;
          height: 20px;
          font-size: 20px;
          color: pick-color('primary', 'web');
        }
      }
    }
  }
}
